import { ref, onMounted } from "vue";
import axios from "axios";
import { useRouter } from "vue-router";
export default {
  setup() {
    const picturewidth = ref();
    picturewidth.value = 0.8 * window.innerWidth;
    const title_head = ref("");
    const router = useRouter();
    const show = ref(false);
    const show_1 = ref(true);
    const onClickLeft = () => router.replace("/User");
    const picture = () => show.value = true;
    const imageUrl = ref("");
    const title = ref("");
    const desc = ref("");
    const credit = ref("");
    const registerTime = ref("");
    const updateTime = ref("");
    const token = localStorage.getItem("jwtToken"); // ‰ªélocalStorageËé∑ÂèñJWT‰ª§Áâå
    if (!token) {
      router.replace("/login");
    }
    const headers = {
      Authorization: `Bearer ${token}`
    };
    const fetchLoginToken = () => {
      axios.post("/api/loginToken?token=" + token).then(response => {
        if (response.data.code == 0) {
          console.error(response.data.data);
          router.replace("/login");
          return;
        }
        console.log(response.data.data);
        // Âú®ËøôÈáåÂ§ÑÁêÜÁôªÂΩï‰ª§ÁâåÊé•Âè£ÁöÑÂìçÂ∫î
        // Â¶ÇÊûúÈúÄË¶ÅÊâßË°å‰∏Ä‰∫õÁâπÂÆöÁöÑÊìç‰ΩúÔºåÂèØ‰ª•Âú®ËøôÈáåÊ∑ªÂä†‰ª£Á†Å
      }).catch(error => {
        console.error("ËØ∑Ê±ÇloginTokenÊé•Âè£Â§±Ë¥•", error);
        router.replace("/login");
      });
    };
    const id = router.currentRoute.value.params.id;
    let strippedId = id.replace(":", "");
    const fetchDataAndFillForm = async () => {
      try {
        const response = await axios.get(`/api/selectUser?id=${strippedId}`, {
          headers
        });
        const data = response.data.data; // ÂÅáËÆæÊúçÂä°Âô®ËøîÂõûÁöÑÊï∞ÊçÆÊòØ‰∏Ä‰∏™ÂåÖÂê´‰∏äËø∞Â≠óÊÆµÁöÑÂØπË±°

        // Â°´ÂÖÖË°®ÂçïÂ≠óÊÆµ
        imageUrl.value = data.userImage;
        title.value = data.name;
        title_head.value = title.value;
        if (data.userText == "") {
          desc.value = "üòÇËøô‰∏™‰∫∫ÂæàÊáíÔºåÊ≤°ÊúâÂ°´ÂÜôÁõ∏ÂÖ≥ÁöÑ‰ªªÂä°ËØ¶ÊÉÖÔºÅ";
        }
        desc.value = data.userText;
        credit.value = data.credit;
        registerTime.value = data.registerTime;
        updateTime.value = data.updateTime;
      } catch (error) {
        console.error("Ëé∑ÂèñÊï∞ÊçÆÂ§±Ë¥•", error);
      }
      show_1.value = false;
    };
    // Âú®ÁªÑ‰ª∂Âä†ËΩΩÂÆåÊàêÂêéËá™Âä®Ëß¶ÂèëÊï∞ÊçÆÂä†ËΩΩÂíåÂ°´ÂÖÖ
    onMounted(() => {
      fetchLoginToken();
      fetchDataAndFillForm();
    });
    const logout = () => {
      // Âà†Èô§Êú¨Âú∞Â≠òÂÇ®ÁöÑ token
      localStorage.removeItem("jwtToken");

      // ‰ΩøÁî® Vue Router ËøõË°åÈ°µÈù¢Ë∑≥ËΩ¨Âà∞ÁôªÂΩïÈ°µÈù¢
      router.replace("/login");
    };
    return {
      picturewidth,
      title_head,
      show,
      show_1,
      picture,
      onClickLeft,
      imageUrl,
      title,
      desc,
      credit,
      registerTime,
      updateTime,
      fetchDataAndFillForm,
      logout
    };
  }
};